/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", sans-serif;
}

/* Background */
body {
  min-height: 100vh;
  background: linear-gradient(135deg, #4f46e5, #6366f1);
}

/* Center wrapper */
.app-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Card */
.card {
  background: #ffffff;
  width: 500px;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.2);
  animation: fadeIn 0.6s ease;
}

/* Logo */
.logo {
  text-align: center;
  font-size: 26px;
  font-weight: 700;
  color: #4f46e5;
  margin-bottom: 10px;
}

.logo span {
  font-size: 30px;
}

/* Subtitle */
.subtitle {
  text-align: center;
  color: #555;
  margin-bottom: 20px;
}

/* Inputs */
input {
  width: 100%;
  padding: 12px;
  margin-bottom: 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
}

/* Question count */
.question-count {
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}

/* Question */
.question {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
}

/* Options */
.option {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: 0.2s;
}

.option:hover {
  background: #eef2ff;
  border-color: #4f46e5;
}

.option.selected {
  background: #4f46e5;
  color: white;
  border-color: #4f46e5;
}

/* Buttons */
.btn-group {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

button {
  padding: 10px 18px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
}

.btn-primary {
  background: #4f46e5;
  color: white;
}

.btn-secondary {
  background: #e5e7eb;
}

/* Result */
.result-score {
  font-size: 22px;
  text-align: center;
  margin: 20px 0;
  font-weight: 700;
}

/* Anim */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}